import navBar from "@/components/nav-bar";
import { mapState } from 'vuex';
export default {
	components: {navBar},
	data() {
		return {
			info:null
		}
	},
	computed:{
		...mapState(['hasLogin','userInfo','config'])
	},
	async onPullDownRefresh() {
		uni.showLoading({
			title:"数据加载中..."
		})
		await this.getPromoteInfo();
		uni.stopPullDownRefresh();
		uni.hideLoading();
	},
	methods: {
		async getPromoteInfo(){
			let res = await this.$http({
				apiName:"getPromoteInfo",
			})
			try{
				this.info = res;
			}catch(e){}
		},
		toPath(path){
			uni.navigateTo({
				url:path
			})
		},
		withDraw(){
			if(this.info.isVip == 1){
				uni.navigateTo({
					url:"/pages/withdraw"
				})
			}else{
				uni.navigateTo({
					url:'/pages/join?showWd=1&vipAmount=' + this.info.vipAmount + '&vipMaskNum=' + this.info.vipMaskNum
				})
			}
		}
	},
	async onShow(){
		uni.showLoading({
			title:"数据加载中..."
		})
		await this.getPromoteInfo();
		uni.hideLoading();
	}
}
